@import "./header.scss";
@import "./common.scss";
@import "./footer.scss";
@import "./value.scss";

body{
  .package-block{
    h3{
      height: 40px;
      background:$main_color ;
      line-height: 40px;
      color: $white;
    }
    ul{
      li{
        section{
          padding: 0 20px;
          margin: 20px;
          border: 1px solid $gray_border;
          .des{
            p{
              border-bottom: 1px solid $gray_border;
              height: 40px;
              line-height: 40px;
              text-align: center;
              color: $font_normal_color;
            }
          }
          p:nth-of-type(1){
            font-size: 30px;
            color: $main_color;
            height: 120px;
            line-height: 120px;
          }
          p:nth-of-type(2){
            border-radius: 10px;
            color: $white;
            margin-bottom: 10px;
          }
          p:nth-of-type(3){

          }
          button{
            width: 160px;
            height: 44px;
            background: $main_color;
            margin: 20px auto;
          }

        }
      }
    }
  }
}
